.starter{
	width: 70vw;
	margin:auto;
	min-height: 100vh;
	display: flex;
	align-items: center;
	justify-content: space-evenly;
}


.rocket{
	font-size: 30vh;
	/*transform: rotate(-45deg);*/
}

.description{
	width: 500px;
	font-family: 'Roboto Condensed', sans-serif;
	font-size: x-large;
	letter-spacing: 2px;
}

.gitLink{
	text-decoration: none;
	color: var(--gray0);
}
.gitIcon{
	display: flex;
	align-items: center;
	font-family: monospace;
	margin-top: 40px;
	width: 320px;
	justify-content: space-between;
	padding: 2px 15px;
	background-color: var(--gray6);
	border-radius: 20px;
}

.fireUp-container{
	margin-top: 30px;
	text-align: center;
}
.fire-up{
	border: 3px solid orangered;
	text-decoration: none;
	color: white;
	font-family: fantasy;
	padding: 10px 100px;
	border-radius: 10px;
	transition: background 10s;
	
}

.fire-up:hover{
	background: linear-gradient(90deg,orange,orangered);
	border: none;
}

@media screen and (max-width: 600px){
	.starter{
		flex-direction: column;
		width: 90vw;
		margin-top: 17vh;
	}
	.description{
		width: 100%;
		text-align: center;
	}
	.fireUp-container{
		font-size: large;
	}
}

@media screen and (max-height: 500px){
	.starter{
		margin-top: 17vh;
	}
}
